/** @format */

button {
  display: flex;
  padding: 0.75rem 1.5rem;
  justify-content: center;
  align-items: center;
  gap: 0.625rem;
  color: #fff;
  font-family: inherit;
  font-size: 1rem;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  border-radius: 0.625rem;
  background: #d3874a;
  border: transparent;
  outline: none;
  transition: all 0.5s ease;
}

button:hover {
  background-color: #d3884ab0;
  transition: all 0.5s ease;
  cursor: pointer;
}
